import * as React from "react";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import {BrowserView, MobileView} from "react-device-detect";
import Button from "@mui/material/Button";
import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import {Link} from "gatsby";

import ResumeFile from "../images/resume.pdf";


const modalStyle = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    bgcolor: 'background.paper',
    width: "90%",
    height: "90%",
}

const linkStyle = {
    color: `black`,
    textDecoration: `none`,
    padding: `0px 7px`,
}

export const MainLayout: React.FC = ({children}) => {
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    return (
        <>
            <Toolbar>
                <Typography variant="h4" component="div" sx={{ flexGrow: 1 }}>
                    <Link to={"/"} style={linkStyle}>
                        Dragon Prevost
                    </Link>
                </Typography>
                <Link to={"/projects"} style={linkStyle}>
                    <Button color="inherit">Projects</Button>
                </Link>
                <BrowserView>
                    <Button color="inherit" onClick={handleOpen}>Resume</Button>
                </BrowserView>
                <MobileView>
                    <Button color="inherit" href={ResumeFile} target={"_blank"}>Resume</Button>
                </MobileView>
            </Toolbar>
            {children}
            <Modal open={open} onClose={handleClose}>
                <Box sx={modalStyle}>
                    <object data={ResumeFile} type="application/pdf" height={"100%"} width={"100%"} />
                </Box>
            </Modal>
        </>
    );

}